import axios from "axios";

export default {
  namespaced: true,
  state: {
    products: [],
  },
  mutations: {
    SET_PRODUCTS(state, prods) {
      state.products = prods;
    },
  },
  actions: {
    async obtainProducts(context) {
      const response = await axios.get("/products");
      if (response.status === 200 && response.data.length > 0) {
        context.commit("SET_PRODUCTS", response.data);
      }
    },
    async saveNewProduct(context, prod) {
      let response = null;
      if (prod.id !== undefined) {
        response = await axios.put("/products/" + prod.id, prod);
      } else {
        response = await axios.post("/products", prod);
      }

      if (response.status >= 200 && response.status < 400) {
        context.dispatch("obtainProducts");
      }
    },
    async removeProduct(context, prodId) {
      const response = await axios.delete("/products/" + prodId);
      if (response.status >= 200 && response.status < 400) {
        context.dispatch("obtainProducts");
      }
    },
  },
  getters: {
    getProducts(state) {
      return state.products;
    },
  },
};
