import axios from "axios";

export default {
  namespaced: true,
  state: {
    cart: { products: [] },
    orders: [],
  },
  mutations: {
    LOAD_CART(state, cart) {
      state.cart = cart;
    },
    ADD_TO_CART(state, prod) {
      let inCart = state.cart.products.find((ic) => ic.id === prod.id);
      if (inCart) {
        inCart.quantity++;
        const otherProds = state.cart.products.filter((p) => p.id !== prod.id);
        state.cart.products = [...otherProds, inCart];
      } else {
        prod.quantity = 1;
        state.cart.products.push(prod);
      }
    },
    SUB_FROM_CART(state, prod) {
      let inCart = state.cart.products.find((ic) => ic.id === prod.id);
      if (inCart) {
        inCart.quantity--;
        const otherProds = state.cart.products.filter((p) => p.id !== prod.id);
        if (inCart.quantity === 0) {
          state.cart.products = [...otherProds];
        } else {
          state.cart.products = [...otherProds, inCart];
        }
      }
    },
    SET_ORDERS(state, orders) {
      state.orders = orders;
    },
    RESET_CART(state) {
      let newCart = { ...state.cart };
      newCart.products = [];
      state.cart = newCart;
    },
  },
  actions: {
    async obtainUserCart(context, email) {
      const response = await axios.get("/cart?userEmail=" + email);
      if (response.status === 200 && response.data.length > 0) {
        context.commit("LOAD_CART", response.data[0]);
      } else {
        context.commit("LOAD_CART", { products: [] });
      }
    },

    addToCart(context, prod) {
      context.commit("ADD_TO_CART", prod);
    },

    subFromCart(context, prod) {
      context.commit("SUB_FROM_CART", prod);
    },

    async syncUserCart(context, data) {
      const response = await axios.get("/cart?userEmail=" + data.email);
      let responseUpdate = null;
      if (response.status === 200 && response.data.length > 0) {
        responseUpdate = await axios.put("/cart/" + data.cart.id, data.cart);
      } else {
        responseUpdate = await axios.post("/cart", data.cart);
        context.commit("LOAD_CART", responseUpdate.data);
      }
    },

    async emptyUserCart(context, data) {
      const response = await axios.get("/cart?userEmail=" + data.email);
      if (response.data.length === 1) {
        let emptyCart = { ...response.data[0] };
        emptyCart.products = [];
        await axios.put("/cart/" + emptyCart.id, emptyCart);
      }
    },

    async placeOrder(context, order) {
      let response = null;
      response = await axios.post("/orders", order);
      if (response.status >= 200 && response.status < 400) {
        context.dispatch("obtainOrders");
        context.commit("RESET_CART");
        context.dispatch("emptyUserCart", order);
      }
    },

    async obtainOrders(context, userId) {
      const response = await axios.get("/orders?userId=" + userId);
      if (response.status === 200 && response.data.length > 0) {
        context.commit("SET_ORDERS", response.data);
      }
    },
  },
  getters: {
    getCart(state) {
      let newCart = { ...state.cart };
      newCart.products = state.cart.products.sort((a, b) => {
        if (a.name < b.name) return -1;
        if (a.name > b.name) return 1;
        return 0;
      });
      return newCart;
    },
    getCartCount(state) {
      let total = 0;
      if (state.cart && state.cart.products && state.cart.products.length > 0) {
        state.cart.products.map((p) => (total += p.quantity));
      }
      return total;
    },
    getCartTotal(state) {
      let total = 0.0;
      if (state.cart && state.cart.products && state.cart.products.length > 0) {
        state.cart.products.map((p) => (total += p.quantity * p.price));
      }
      return total;
    },
    getOrders(state) {
      return state.orders;
    },
  },
};
