<template>
  <b-row class="mb-1">
    <b-col cols="4">{{ product.name }}</b-col>
    <b-col cols="3"
      ><b-button
        size="sm"
        variant="warning"
        @click="subOneFromCart"
        :disabled="!btnEnabled"
        >-</b-button
      >
      {{ product.quantity }}
      <b-button
        size="sm"
        variant="info"
        @click="addOneToCart"
        :disabled="!btnEnabled"
        >+</b-button
      ></b-col
    >
    <b-col cols="2">{{ product.price }}</b-col>
    <b-col cols="2">{{
      parseFloat(product.price * product.quantity).toFixed(2)
    }}</b-col>
  </b-row>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  props: {
    product: { type: Object, default: null },
    addToCart: { type: Function },
    subFromCart: { type: Function },
  },
  data() {
    return {
      btnEnabled: true,
    };
  },
  computed: {
    ...mapGetters("auth", { profile: "getProfile" }),
  },
  methods: {
    addOneToCart() {
      this.btnEnabled = false;
      this.$emit("add-to-cart", this.product);
      this.delayBtnUsage();
    },
    subOneFromCart() {
      this.btnEnabled = false;
      this.$emit("sub-from-cart", this.product);
      this.delayBtnUsage();
    },
    delayBtnUsage() {
      let to = setTimeout(() => {
        this.btnEnabled = true;
        clearTimeout(to);
      }, 1000);
    },
  },
};
</script>

