<template>
  <b-card
    :title="product.name"
    :img-src="imgUrl"
    :img-alt="product.name"
    img-top
    tag="article"
    style="min-width: 20rem; min-height: 25rem; max-width: 24rem"
    class="mb-2"
  >
    <b-card-text> Price: ${{ product.price }} </b-card-text>

    <b-button
      size="sm"
      href="#"
      variant="info"
      @click="$bvModal.show('modal-' + product.id)"
      >More info</b-button
    >
    <b-button
      v-show="profile"
      size="sm"
      variant="primary"
      @click="onAddOneToCart"
      class="ml-2"
      >Add 1 to cart</b-button
    >
    <b-modal
      :id="'modal-' + product.id"
      :title="product.name"
      ok-only
      button-size="sm"
    >
      <CatalogItemDetail :product="product" />
    </b-modal>
  </b-card>
</template>

<script>
import { mapGetters } from "vuex";
import CatalogItemDetail from "./CatalogItemDetail.vue";
export default {
  components: { CatalogItemDetail },
  props: {
    product: { type: Object, default: null },
  },
  computed: {
    ...mapGetters("auth", { profile: "getProfile" }),
    ...mapGetters("cart", { cart: "getCart" }),
    imgUrl() {
      return require("@/assets/imgs/" + this.product.img);
    },
  },
  methods:{
    async onAddOneToCart() {
      await this.$store.dispatch("cart/addToCart", this.product);
      await this.$store.dispatch("cart/syncUserCart", {
        email: this.profile.email,
        cart: this.cart,
      });
      this.showAlert();
    },
  }
};
</script>

