import Vue from "vue";
import VueRouter from "vue-router";

import AuthView from "@/views/AuthView";
import HomeView from "@/views/HomeView";
import CartView from "@/views/CartView";
import OrderView from "@/views/OrderView";
import AdminView from "@/views/AdminView";

import OrderForm from "@/components/OrderForm.vue";
import Orders from "@/components/Orders.vue";

Vue.use(VueRouter);

const routes = [
  { path: "/", name: "home", component: HomeView, props: true },
  { path: "/login", name: "auth", component: AuthView, props: true },
  { path: "/cart", name: "cart", component: CartView, props: true },
  {
    path: "/order",
    component: OrderView,
    children: [
      { path: "close", component: OrderForm },
      { path: "list", component: Orders },
    ],
  },
  { path: "/order", name: "order", component: OrderView, props: true },
  { path: "/admin", name: "admin", component: AdminView, props: true },
];

const router = new VueRouter({
  routes,
  base: process.env.BASE_URL,
  mode: "history",
});

export default router;
