import axios from "axios";

export default {
  namespaced: true,
  state: {
    users: [],
  },
  mutations: {
    SET_USERS(state, users) {
      state.users = users;
    },
  },
  actions: {
    async obtainUsers(context) {
      const response = await axios.get("/users");
      if (response.status === 200 && response.data.length > 0) {
        context.commit("SET_USERS", response.data);
      }
    },
  },
  getters: {
    getUsers(state) {
      return state.users;
    },
  },
};
