<template>
  <div>
    <b-form @submit.prevent="onPlaceOrder">
      <h4>Place your order</h4>
      <p><b>Quantity:</b> {{ cartCount }} items</p>
      <p><b>Order total:</b> $ {{ cartTotal }}</p>
      <h5>Please, state your delivery address and phone number</h5>
      <b-form-group id="input-group-1" label="Address" label-for="address">
        <b-form-input
          id="address"
          v-model="order.address"
          type="text"
          placeholder="ie: 765 Siempre Viva Av"
          required
        ></b-form-input>
        <b-form-invalid-feedback :state="validateAddress">
          Please, check the address format
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validateAddress">
          It's OK
        </b-form-valid-feedback>
      </b-form-group>
      <b-form-group id="input-group-2" label="Phone" label-for="phone">
        <b-form-input
          id="phone"
          v-model="order.phone"
          type="tel"
          placeholder="ie: +54 11 5432 9876"
          required
        ></b-form-input>
        <b-form-invalid-feedback :state="validatePhone">
          Please, check the phone format
        </b-form-invalid-feedback>
        <b-form-valid-feedback :state="validatePhone">
          It's OK
        </b-form-valid-feedback>
      </b-form-group>
      <b-button type="submit" variant="primary" :disabled="isProcessing">
        <b-spinner small v-show="isProcessing" />Place order
      </b-button>
      <b-button
        type="button"
        variant="secondary"
        to="/cart"
        :disabled="isProcessing"
        >Cancel</b-button
      >
    </b-form>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters("cart", {
      cart: "getCart",
      cartTotal: "getCartTotal",
      cartCount: "getCartCount",
    }),
    ...mapGetters("auth", { profile: "getProfile" }),
    validateAddress() {
      return this.order.address.trim() !== "";
    },
    validatePhone() {
      return this.order.phone.trim() !== "";
    },
    validateForm() {
      return this.validateAddress && this.validatePhone;
    },
  },
  data() {
    return {
      order: {
        address: "",
        phone: "",
      },
      isProcessing: false,
    };
  },
  methods: {
    async onPlaceOrder() {
      this.$store.dispatch("cart/emptyUserCart", { email: this.profile.email });
      this.isProcessing = true;
      if (this.validateForm) {
        const newOrder = this.order;
        newOrder.date = this.$dayjs().format("YYYY-MM-DD HH:mm:ss");
        newOrder.userId = this.profile.id;
        newOrder.email = this.profile.email;
        newOrder.items = this.cart.products;
        await this.$store.dispatch("cart/placeOrder", newOrder);
        alert("Order placed successfully");

        this.$router.push("/order/list");
      }
      this.isProcessing = false;
    },
  },
};
</script>

